import { Link } from "react-router-dom";
import { Brightness2, WbSunny } from '@material-ui/icons';
import ThemeContext from "../store/theme-context";
import { useContext } from "react";

export default function Nav() {

		const { nightTheme, setNightTheme } = useContext(ThemeContext);

    return (
        <nav className="flex justify-around py-6 text-lg">
            <div>Shivam</div>
            <ul className="flex">
                <li className="px-3">
                    <Link to="/">Home</Link>
                </li>
                <li className="px-3">
                    <Link to="/about">About</Link>
                </li>
                <li className="px-3">
                    <Link to="/skills">Skills</Link>
                </li>
                <li className="px-3">
                    <Link to="/contact">Contactme</Link>
                </li>
                <li onClick={setNightTheme} className="px-3">
                    {
                        nightTheme ? <Brightness2 /> : <WbSunny /> 
                    }
                </li>
            </ul>
        </nav>
    )
}
