import { Star, StarBorder } from "@material-ui/icons"
import { useContext } from "react";
import ThemeContext from "../store/theme-context";

export default function TechTab({ name, exp, starFilled }) {

		const { nightTheme } = useContext(ThemeContext);

    return (
        <div className={`p-4 w-3/4 m-auto rounded-xl my-4 shadow ${nightTheme ? 'bg-blue-200 text-black' : 'bg-white'}`}>
            <div className="flex justify-between">
                <h3 className="font-bold text-xl">{name}</h3>
                <div className="text-yellow-600">
                {
                    Array.from(Array(5).keys()).map((i) => {
                        if (i < starFilled) {
                            return <Star key={i}/>
                        } 
                        return <StarBorder key={i} />
                    })
                }
                </div>
            </div>
            <h4 className="mt-2">Years of experience : {exp}</h4>
        </div>
    )
}
